<template>
  <div class="d-flex align-items-center justify-content-between">
    <div class="flex-grow-1 text-center">
      <p
        v-if="timerEnded"
        class="alert alert-danger my-1 fw-bold mx-3 py-0 text-nowrap"
      >
        Time's up!
      </p>
      <div v-else class="fw-bold fs-4 me-3 my-0">{{ formattedTime }}</div>
    </div>
    <button
      class="btn btn-outline-custom fw-bold py-0 me-2"
      @click="toggleAudio"
      title="Toggle audio"
    >
      <span
        :class="audioEnabled ? 'bi bi-volume-up' : 'bi bi-volume-mute'"
      ></span>
    </button>
    <div class="btn-group gap-1 w-100" role="group">
      <button
        class="btn btn-outline-custom fw-bold py-0"
        @click="decrementTime"
        title="Decrease by a minute"
      >
        -
      </button>
      <button
        class="btn btn-outline-custom fw-bold py-0"
        @click="incrementTime"
        title="Increase by a minute"
      >
        +
      </button>
      <button class="btn btn-outline-custom fw-bold py-0" @click="resetTime">
        Reset
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "CountdownTimer",
  data() {
    return {
      minutes: 0,
      seconds: 0,
      timer: null,
      timerEnded: false,
      audio: null,
      audioEnabled: false,
    };
  },
  computed: {
    formattedTime() {
      // Format the time as MM:SS
      const minutes = this.minutes;
      const seconds = this.seconds < 10 ? "0" + this.seconds : this.seconds;
      return `${minutes}:${seconds}`;
    },
  },
  methods: {
    startTimer() {
      // Start the countdown timer
      if (this.timer) return; // If the timer is already running, do nothing
      this.timer = setInterval(() => {
        if (this.seconds === 0) {
          if (this.minutes === 0) {
            // If both minutes and seconds are zero, stop the timer
            clearInterval(this.timer);
            this.timer = null;
            this.timerEnded = true; // Set the timerEnded flag to true
            if (this.audioEnabled) {
              this.audio.play(); // Play the alarm sound if audio is enabled
            }
            setTimeout(() => {
              this.timerEnded = false; // Reset the timerEnded flag after 3 seconds
            }, 3000);
          } else {
            // If minutes are not zero, decrement minutes and reset seconds to 59
            this.minutes--;
            this.seconds = 59;
          }
        } else {
          // If seconds are not zero, decrement seconds
          this.seconds--;
        }
      }, 1000); // Run the interval every 1 second
    },
    incrementTime() {
      // Increment the minutes and start the timer
      this.minutes++;
      this.startTimer();
    },
    decrementTime() {
      // Decrement the minutes if greater than zero
      if (this.minutes > 0) {
        this.minutes--;
      }
    },
    resetTime() {
      // Reset the timer to 0 minutes and 0 seconds
      this.minutes = 0;
      this.seconds = 0;
      clearInterval(this.timer); // Clear the interval timer
      this.timer = null;
      this.timerEnded = false; // Reset the timerEnded flag
    },
    toggleAudio() {
      if (!this.audioEnabled) {
        if (!this.audio) {
          this.audio = new Audio(require("@/assets/alarm.mp3"));
        }
        this.audio.context.resume().then(() => {
          this.audioEnabled = true;
          console.log("Audio enabled");
        });
      } else {
        this.audioEnabled = false;
        console.log("Audio disabled");
      }
    },
  },
};
</script>
